const localStorageKeyname = 'iconListModel2'
const iconArray2: icon[] = [
  { name: '', icon: 'gongzi', color: 'jiaotong' },
  { name: '', icon: 'jianzhi', color: 'jiushui' },
  { name: '', icon: 'licai', color: 'kanbing' },
  { name: '', icon: 'lijin', color: 'jianshen' },
  { name: '', icon: 'qitashouru', color: 'huafei' }
]

const iconListModel2: IconListModel = {
  data: iconArray2,
  fetch() {
    this.data = JSON.parse(window.localStorage.getItem(localStorageKeyname) || '[]')
    return this.data
  },
  save() {
    window.localStorage.setItem(localStorageKeyname, JSON.stringify(this.data))
  }
}

export default iconListModel2
